<template>
    <div class="form-in-event-search">
        <el-form>
            <el-form-item label="时间">
                <el-date-picker
                    v-model="value1"
                    type="datetime"
                    placeholder="选择起始日期时间"
                    align="right"
                    :picker-options="pickerOptions"
                    size="mini">
                </el-date-picker>
                <el-date-picker
                    v-model="value2"
                    type="datetime"
                    placeholder="选择结束日期时间"
                    align="right"
                    :picker-options="pickerOptions"
                    size="mini">
                </el-date-picker>
            </el-form-item>
            <label class="label-in-event-search">搜索方式</label>
            <el-form-item>
                <el-radio v-model="radio" label="1">按数据源</el-radio>
                <el-radio v-model="radio" label="2">按名称</el-radio>
            </el-form-item>
            <el-form-item>
                <el-select v-model="sourceValue" clearable placeholder="请选择事件源类型" size="small" class="event-source-selector">
                    <el-option
                        v-for="item in sourceOptions"
                        :key="item.sourceValue"
                        :label="item.sourceLabel"
                        :value="item.sourceValue">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <label class="label-in-event-search">事件级别</label>
                <el-checkbox-group v-model="checkListeventlevel">
                    <el-checkbox label="紧急"></el-checkbox>
                    <el-checkbox label="重要"></el-checkbox>
                    <el-checkbox label="一般"></el-checkbox>
                    <el-checkbox label="未定义"></el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="状态">
                <el-select v-model="statusValue" clearable placeholder="请选择事件状态"
                            size="small" class="event-status-selector">
                    <el-option
                        v-for="item in statusOptions"
                        :key="item.statusValue"
                        :label="item.statusLabel"
                        :value="item.statusValue">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-button type="danger" size="mini" class="submit-button-event-search">确认</el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "FormInEventSearch",
        data() {
            return {
                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                sourceOptions: [{
                    sourceValue: '选项1',
                    sourceLabel: 'OPC'
                }, {
                    sourceValue: '选项2',
                    sourceLabel: 'SIS'
                }, {
                    sourceValue: '选项3',
                    sourceLabel: 'GAS'
                }, {
                    sourceValue: '选项4',
                    sourceLabel: '摄像头'
                }, {
                    sourceValue: '选项5',
                    sourceLabel: '服务器'
                }],
                sourceValue: '',

                statusOptions: [{
                    statusValue: '选项1',
                    statusLabel: '已恢复'
                }, {
                    statusValue: '选项2',
                    statusLabel: '未恢复'
                }],
                statusValue: '',

                value1: '',
                value2: '',
                value3: '',
                radio: '1',
                checkListeventlevel:['选中且禁用','复选框 A'],
            };
        }
    };
</script>

<style>
    .el-form-item__label {
        color:white;
    }
    .label-in-event-search {
        color: white;
    }
    .form-in-event-search {
        padding: 4px;
    }
    .submit-button-event-search {
        width: 209px;
        height: 30px;
        font-family: 幼圆;
        border-radius: 2px;
    }
</style>
